import React from 'react';
import CardWrapper from '../components/CardWrapper/CardWrapper.tsx';
import BasicTabs from '../components/BasicTabs/BasicTabs.tsx';
import CustomizedButton from '../components/CustomizedButton/CustomizedButton.tsx';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import styled from 'styled-components';
import { Chip as MuiChip } from '@mui/material';

const Projects: React.FC<{}> = () => {
    const CourseLink = styled.a`
        color: #666;
        opacity: 0.7;
        display: flex;
        &:hover {
            text-decoration: underline;
        }
        & svg {
            font-size: 15px;
        }
    `;

    const OpenIcon = styled(OpenInNewIcon)`
        font-size: 14px;
        color: #666;
    `;

    const CardsContainer = styled.div`
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 20px;
        padding: 10px;
        @media (max-width: 768px) {
            gap: 0px;
            padding: 0px;
        }
    `;

    const CardItem = styled.div`
        flex: 1 1 calc(40% - 20px);  /* Decreased card width */
        max-width: calc(40% - 100px);  /* Decreased card width */
        display: flex;
        justify-content: center;

        @media (max-width: 768px) {
            flex: 1 1 100%;
            max-width: 100%;
        }
    `;

    const CustomCardWrapper = styled(CardWrapper)`
        background-color: white;
        padding: 20px;
        margin: 10px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        border: 1px solid #eae7e1;
        width: 100%;
    `;

    const CustomButton = styled(CustomizedButton)`
        border-radius: 20px;
        padding: 5px;
    `;

    const Chip = styled(MuiChip)`
        margin-top: 6px;
        margin-bottom: 6px;
        margin-right: 6px;
    `;

    const ProjectTitle = styled.p`
        font-size: 14px;
        font-weight: bold;
        color: #666;
    `;

    const ProjectDescription = styled.p`
        color: #666;
        font-size: 14px;
    `;

    const tabs = [
        { 
            label: 'All', 
            content: 
                <CardsContainer>
                    <CardItem>
                        <CustomCardWrapper id="project-card-1"
                            topRightContent={
                                <CustomButton>
                                    <CourseLink href="https://github.com/akash-2020/gmail-clone" target="_blank" rel="noopener noreferrer">
                                        <OpenIcon />
                                    </CourseLink>
                                </CustomButton>
                            }>
                            <ProjectTitle>Gmail Clone</ProjectTitle>
                            <Chip label="Reactjs" size='small' sx={{color: '#666'}}/>
                            <Chip label="Redux" size='small' sx={{color: '#666'}}/>
                            <ProjectDescription>
                                I created a Gmail clone using React.js, Firebase Authentication, and Redux. Users can securely log in with their Gmail credentials, compose and view emails, and manage their inbox. The application features responsive design for seamless usability across devices and integrates real-time updates via Firebase. This project demonstrates my skills in front-end development, state management with Redux, and leveraging Firebase for authentication and real-time data handling.
                            </ProjectDescription>
                        </CustomCardWrapper>
                    </CardItem>
                    <CardItem>
                        <CustomCardWrapper id="project-card-1"
                            topRightContent={
                                <CustomButton>
                                    <CourseLink href="https://github.com/akash-2020/gmail-clone" target="_blank" rel="noopener noreferrer">
                                        <OpenIcon />
                                    </CourseLink>
                                </CustomButton>
                            }>
                            <ProjectTitle>Gmail Clone</ProjectTitle>
                            <Chip label="Reactjs" size='small' sx={{color: '#666'}}/>
                            <Chip label="Redux" size='small' sx={{color: '#666'}}/>
                            <ProjectDescription>
                                I created a Gmail clone using React.js, Firebase Authentication, and Redux. Users can securely log in with their Gmail credentials, compose and view emails, and manage their inbox. The application features responsive design for seamless usability across devices and integrates real-time updates via Firebase. This project demonstrates my skills in front-end development, state management with Redux, and leveraging Firebase for authentication and real-time data handling.
                            </ProjectDescription>
                        </CustomCardWrapper>
                    </CardItem>
                </CardsContainer> 
        },
        { 
            label: 'Full stack', 
            content: 
                <CardsContainer>
                    <CardItem>
                        <CustomCardWrapper id="project-card-3">
                            <ProjectTitle>Project 1</ProjectTitle>
                        </CustomCardWrapper>
                    </CardItem>
                </CardsContainer>
        }
    ];

    return (
        <div>
            <BasicTabs tabs={tabs}></BasicTabs>
        </div>
    );
}

export default React.memo(Projects);